@use "../../styles/variables";

.homeNavigation {
  padding: 30px 0;
  box-sizing: border-box;
  width: 100%;

  ul {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
  }

  li {
    width: calc(50% - 30px);
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    overflow: hidden;
    border-radius: 5px;
    margin: 15px;
    transition: .1s ease-out;
    border: 1px solid lightgrey;
    box-sizing: border-box;
    background-color: lightgrey;

    &:hover {
      box-shadow: 0 0 6px 1px rgba(0,0,0,0.7);
      transform: scale(1.01);
    }

    &.front {
      background-image: url("../../assets/backgounds/front.jpg");
    }

    &.ide {
      background-image: url("../../assets/backgounds/ide.jpg");
    }

    &.html {
      background-image: url("../../assets/backgounds/html.jpg");
    }

    &.css {
      background-image: url("../../assets/backgounds/css.jpg");
    }

    &.js {
      background-image: url("../../assets/backgounds/js.jpg");
    }

    @media (max-width: 768px) {
      width: 100%;
      margin: 25px 0;
    }
  }

  a {
    display: flex;
    box-sizing: border-box;
    padding: 70px 30px;
    border-radius: 5px;
    font-size: 40px;
    line-height: 52px;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
    background: rgba(0, 0, 0, .8);
    transition: .1s ease-out;

    &:hover {
      background: rgba(0, 0, 0, .3);
    }

    @media (max-width: 768px) {
      font-size: 30px;
      line-height: 42px;
      padding: 50px 20px;
    }
  }
}

.headerNavigation {
  display: flex;
  user-select: none;

  ul {
    display: flex;
  }

  li {
    margin: 0 10px;
  }

  a {
    font-size: 16px;

    &:visited {
      color: variables.$text-color;
    }

    &:hover {
      color: variables.$primary-color;
    }
  }

  .active {
    color: variables.$text-color !important;
    opacity: .5;
    cursor: default !important;
  }

  @media (max-width: 568px) {
    display: none;
  }
}
