.root {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @keyframes rotate {
    from {
      transform: rotate(0);
    }

    to {
      transform: rotate(360deg);
    }
  }

  img:first-of-type {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    background-color: lightgray;
    margin: 40px 0;
    animation: rotate 4s linear infinite;
  }

  p {
    font-size: 20px;
    line-height: 26px;
    margin: 10px 0 40px;
    max-width: 500px;
    text-align: center;
  }
}

.linkContainer {
  display: flex;
  flex-wrap: wrap;
  margin: 40px 0;
  justify-content: center;
  align-items: center;

  a {
    font-size: 70px;
    line-height: 70px;
    margin: 20px;
    text-align: center;

    @media (max-width: 768px) {
      font-size: 50px;
      line-height: 50px;
    }
  }
}


