.root {
  display: none;
  justify-content: center;
  align-items: center;
  background: tomato;
  width: 60px;
  height: 60px;
  border: none;
  outline: none;
  border-radius: 50%;
  position: fixed;
  right: 30px;
  bottom: 100px;
  cursor: pointer;
  z-index: 100;
  transform: translateX(0);


  svg {
    transform: scale(1.5);
  }

  &:hover {
    box-shadow: 0 0 5px 0 rgba(0,0,0,0.6);
  }
}

.isShow {
  display: flex;

  @media (max-width: 567px) {
    display: none;
  }
}
