.imgContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  cursor: pointer;
  border-radius: 4px;
  overflow: hidden;

  &:hover {
    box-shadow: 0 0 17px 0 rgba(34, 60, 80, 0.2);
  }

  img {
    width: 100%;
  }
}

.fullWidth {
  position: fixed;
  width: 100vw !important;
  height: 100vh !important;
  background: rgba(0, 0, 0, .8);
  z-index: 1000;
  top: 0;
  left: 0;
  box-shadow: unset;
  border-radius: unset;

  img {
    width: 95% !important;
    height: 95vh !important;
    object-fit: contain;
  }

  @media (max-width: 768px) {
    width: 100vw !important;
  }
}
