.root {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.wrapper {
  width: 40%;
  margin: 20px;

  @media (max-width: 768px) {
    width: 90%;
  }
}
