.root {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .logo {
    width: 150px;
    margin: 40px 0;
  }

  a {
    width: 100% !important;
    text-align: left;
  }
}

.wrapper {
  width: 100%;
  display: flex;

  div {
    width: 20%;
    margin-left: 20px;
  }
}

.paragraph {
  width: 100%;
  font-size: 20px;
  line-height: 26px;
  margin: 10px 0;
}

.imgWrapper {
  width: 40%;
  margin: 10px 0 30px;

  @media (max-width: 768px) {
    width: 90%;
    margin: 30px 0 10px;
  }
}
