.root {
  width: 100%;
  height: 54px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  padding: 0 36px;
  position: fixed;
  top:0;
  left: 0;
  z-index: 10;
  background: #fff;

  img {
    height: 20px;
  }

  span {
    font-size: 14px;
    color: #0f0f0f;
  }

  .logoLink {
    display: block;
    height: 20px;
  }
}

.moved {
  box-shadow: 0 4px 23px 0 rgba(0,0,0,0.32);
}
