.root {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .logo {
    height: 150px;
    margin: 40px 0;
  }

  a {
    width: 100% !important;
    text-align: left;
  }

  ol {
    width: 100%;
    padding: 0 36px;
  }

  li {
    margin: 10px 36px;
    font-size: 18px;
    line-height: 26px;
  }
}

.imgWrapper {
  width: 40%;

  @media (max-width: 768px) {
    width: 90%;
  }
}
