.root {
  width: 100%;
  box-sizing: border-box;
  padding: 20px 0 60px;
}

.button {
  width: fit-content;
  padding: 6px 12px;
  box-sizing: border-box;
  border-radius: 5px;
  filter: brightness(1);
  font-size: 16px;
  cursor: pointer;
  margin-bottom: 20px;
  margin-right: 20px;

  &:hover {
    filter: brightness(1.1);
  }
}
