@use "./variables";

* {
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: transparent;
}

body {
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: variables.$text-color;
}

a {
  text-decoration: none;
  color: variables.$text-color;
  font-size: 18px;
  line-height: 22px;

  &:visited {
    color: variables.$secondary-color;
  }

  &:hover {
    color: variables.$primary-color;
  }


}

ul {
  list-style: none;
}

h1 {
  width: 100%;
  text-align: center;
  font-size: 36px;
  line-height: 54px;
}

p {
  font-size: 19px;
  line-height: 24px;
}

section.root {
  padding: 20px 0;
  width: 100%;
}

.heading {
  width: 100%;
  text-align: left;
  margin: 50px 0 20px;
  font-size: 28px;
  line-height: 42px;
  font-weight: 700;

  @media (max-width: 576px) {
    font-size: 24px;
    line-height: 36px;
  }
}

.paragraph {
  width: 100%;
  font-size: 20px;
  line-height: 26px;
  margin: 10px 0;

  @media (max-width: 576px) {
    font-size: 16px;
    line-height: 24px;
  }
}

pre {
  width: 100%;
  box-sizing: border-box;
}

